
@charset "UTF-8";
@import url("https://d1azc1qln24ryf.cloudfront.net/26905/Studyportalslineariconset/style-cf.css?p6k6l4");
/* Brand orange colors. */
.multiselect__tag-icon:after, .multiselect__option:after, .multiselect__option--group {
  font-size: 0.8125rem;
  line-height: 1rem;
}
.multiselect,
.multiselect__input,
.multiselect__single,
.multiselect__option,
.multiselect__placeholder, .multiselect__tags, .multiselect__select:before, .multiselect__content-wrapper {
  font-size: 0.9375rem;
  line-height: 1.5rem;
}
fieldset[disabled] .multiselect {
  pointer-events: none;
}
.multiselect__spinner {
  position: absolute;
  right: 1px;
  top: 1px;
  width: 48px;
  height: 35px;
  background: #FFFFFF;
  display: block;
}
.multiselect__spinner:before,
.multiselect__spinner:after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  margin: -8px 0 0 -8px;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  border-color: #f95c39 transparent transparent;
  border-style: solid;
  border-width: 2px;
  box-shadow: 0 0 0 1px transparent;
}
.multiselect__spinner:before {
  animation: spinning 2.4s cubic-bezier(0.41, 0.26, 0.2, 0.62);
  animation-iteration-count: infinite;
}
.multiselect__spinner:after {
  animation: spinning 2.4s cubic-bezier(0.51, 0.09, 0.21, 0.8);
  animation-iteration-count: infinite;
}
.multiselect__loading-enter-active,
.multiselect__loading-leave-active {
  transition: opacity 0.4s ease-in-out;
  opacity: 1;
}
.multiselect__loading-enter,
.multiselect__loading-leave-active {
  opacity: 0;
}
.multiselect,
.multiselect__input,
.multiselect__single,
.multiselect__placeholder {
  font-family: inherit;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  margin: 0.25rem 0;
}
.multiselect {
  box-sizing: content-box;
  display: block;
  position: relative;
  width: 100%;
  min-height: 40px;
  text-align: left;
  color: #929699;
  margin: 0.5rem 0;
}
.multiselect * {
  box-sizing: border-box;
}
.multiselect:focus {
  outline: none;
}
.multiselect--disabled {
  pointer-events: none;
  opacity: 0.6;
}
.multiselect--active {
  z-index: 50;
}
.multiselect--active .multiselect__tags {
    border-color: #929699;
}
.multiselect--active .multiselect__select {
  transform: rotateX(180deg);
}
.multiselect__input,
.multiselect__single,
.multiselect__placeholder {
  position: relative;
  display: inline-block;
  min-height: 20px;
  line-height: 20px;
  border: none;
  border-radius: 3px;
  background: #FFFFFF;
  padding-top: 2px;
  width: 100%;
  transition: border 0.1s ease;
  box-sizing: border-box;
  vertical-align: middle;
}
.multiselect__input::-webkit-input-placeholder {
  color: #929699;
}
.multiselect__input:-ms-input-placeholder {
  color: #929699;
}
.multiselect__input::placeholder {
  color: #929699;
}
.multiselect__tag ~ .multiselect__input,
.multiselect__tag ~ .multiselect__single {
  width: auto;
}

/* CAN BE REMOVeD */
.multiselect__input:hover,
.multiselect__single:hover {
  border-color: #cfcfcf;
}
.multiselect__input:focus,
.multiselect__single:focus {
  border-color: #a8a8a8;
  outline: none;
}

/* END OF UNNEEDED CODE */
.multiselect__single {
  padding-left: 5px;
  margin-bottom: 8px;
}
.multiselect__tags-wrap {
  display: inline;
}
.multiselect__input {
  display: block !important;
}
.multiselect__tags {
  min-height: 40px;
  padding: 0.25rem 2rem 0.25rem 0.5rem;
  background: #FFFFFF;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid #C4C9CC;
}
.multiselect__tag {
  position: relative;
  display: inline-block;
  padding: 0.25rem 2rem 0.25rem 0.5rem;
  border-radius: 3px;
  margin: 0.25rem 0.5rem 0.25rem 0;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  max-width: 100%;
  text-overflow: ellipsis;
  vertical-align: middle;
  color: #929699;
  border: 1px solid #929699;
}
.multiselect__tag-icon {
  cursor: pointer;
  margin-left: 0.5rem;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  font-weight: 700;
  font-style: initial;
  width: 1.5rem;
  text-align: center;
  line-height: 1.5rem;
  transition: all 0.2s ease;
  border-radius: 3px;
}
.multiselect__tag-icon:after {
  content: "??";
  color: #929699;
  vertical-align: middle;
}
.multiselect__tag-icon:focus,
.multiselect__tag-icon:hover {
  background: #C4C9CC;
}
.multiselect__tag-icon:focus:after,
.multiselect__tag-icon:hover:after {
  color: #FFFFFF;
}

/* Looks unused */
.multiselect__current {
  line-height: 16px;
  min-height: 40px;
  box-sizing: border-box;
  display: block;
  overflow: hidden;
  padding: 8px 12px 0;
  padding-right: 30px;
  white-space: nowrap;
  margin: 0;
  text-decoration: none;
  border-radius: 3px;
  border: 1px solid #E8E8E8;
  cursor: pointer;
}

/* End of unused code */
.multiselect__select {
  line-height: 16px;
  display: block;
  position: absolute;
  box-sizing: border-box;
  width: 2rem;
  right: 1px;
  top: 1px;
  bottom: 1px;
  padding: 0.5rem;
  margin: 0;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  transition: transform 0.2s ease;
}
.multiselect__select:before {
  position: absolute;
  left: 50%;
  top: 50%;
  line-height: 1;
  color: #929699;
  content: "\E93A";
  font-family: icomoon;
  transform: translate(-50%, -50%);
}
.multiselect__placeholder {
  color: #ADADAD;
}
.multiselect--active .multiselect__placeholder {
  display: none;
}
.multiselect__content-wrapper {
  border: 1px solid #DCE1E5;
  border-radius: 3px;
  box-shadow: 0 2px 4px rgba(3, 25, 37, 0.234375);
  position: absolute;
  display: block;
  background: #FFFFFF;
  width: 100%;
  max-height: 240px;
  overflow: auto;
  z-index: 200;
  -webkit-overflow-scrolling: touch;
  margin-top: 0.5rem;
}
.multiselect--above .multiselect__content-wrapper {
    bottom: 100%;
    box-shadow: 0 -1px 1px rgba(3, 25, 37, 0.234375);
    margin-bottom: 0.5rem;
}
.multiselect__content {
  list-style: none;
  display: inline-block;
  padding: 0;
  margin: 0.5rem 0;
  min-width: 100%;
  vertical-align: top;
}
.multiselect__content::webkit-scrollbar {
  display: none;
}
.multiselect__element {
  cursor: pointer;
  display: block;
  margin: 0;
}
.multiselect__option {
  cursor: pointer;
  display: block;
  padding: 0.25rem 1rem;
  position: relative;
  min-height: 1rem;
  text-decoration: none;
  text-transform: none;
  color: #313233;
}
.multiselect__option:after {
  top: 0;
  right: 0;
  position: absolute;
  line-height: 1.5rem;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
}
.multiselect__option--highlight {
  background: #f95c39;
  outline: none;
  color: #FFFFFF;
}
.multiselect__option--highlight:after {
  content: attr(data-select);
  background: #f95c39;
  color: #FFFFFF;
}
.multiselect__option--selected {
  background: #EDF2F4;
  color: #929699;
  font-weight: bold;
}
.multiselect__option--selected:after {
  content: attr(data-selected);
  color: #C4C9CC;
}
.multiselect__option--selected.multiselect__option--highlight,
.multiselect__option--group-selected.multiselect__option--highlight {
  background: #CC4C2F;
  color: #FFFFFF;
  font-weight: 400;
}
.multiselect__option--selected.multiselect__option--highlight:after,
.multiselect__option--group-selected.multiselect__option--highlight:after {
  background: #CC4C2F;
  content: attr(data-deselect);
  color: #FFFFFF;
}
.multiselect--disabled:not(.multiselect__option--group) {
  background: #E8ECED;
  pointer-events: none;
}
.multiselect--disabled .multiselect__current,
.multiselect--disabled .multiselect__select {
  background: #E8ECED;
  color: #C4C9CC;
}
.multiselect__option--disabled:not(.multiselect__option--group) {
  background: #E8ECED;
  color: #C4C9CC;
  cursor: text;
  pointer-events: none;
}
.multiselect__option--group {
  color: #929699;
  padding-left: 0.5rem;
  text-transform: capitalize;
}
.multiselect__option--group.multiselect__option--highlight {
  color: #FFFFFF;
}
.multiselect__option--disabled.multiselect__option--highlight {
  background: #dedede;
}
.multiselect-enter-active,
.multiselect-leave-active {
  transition: all 0.15s ease;
}
.multiselect-enter,
.multiselect-leave-active {
  opacity: 0;
}
.multiselect__strong {
  margin-bottom: 8px;
  line-height: 20px;
  display: inline-block;
  vertical-align: top;
}
*[dir="rtl"] .multiselect {
  text-align: right;
}
*[dir="rtl"] .multiselect__select {
  right: auto;
  left: 1px;
}
*[dir="rtl"] .multiselect__tags {
  padding: 8px 8px 0px 40px;
}
*[dir="rtl"] .multiselect__content {
  text-align: right;
}
*[dir="rtl"] .multiselect__option:after {
  right: auto;
  left: 0;
}
*[dir="rtl"] .multiselect__clear {
  right: auto;
  left: 12px;
}
*[dir="rtl"] .multiselect__spinner {
  right: auto;
  left: 1px;
}
@keyframes spinning {
from {
    transform: rotate(0);
}
to {
    transform: rotate(2turn);
}
}



